
@import './settings.scss';
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  color: $color-text;
  transition: .3s;

  &:hover {
    color: $color-text;
  }

  &.active {
    color: $color-text;
    font-weight: bold;
  }
}