@import url(https://fonts.googleapis.com/css?family=Exo+2:800,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.logo{font-family:'Exo 2', sans-serif;font-weight:900;font-size:25.2px;color:#333}.burger{display:none}@media (max-width: 767px){.burger{display:inline-block}}

.main-menu{display:flex;flex-wrap:wrap;list-style-type:none;padding:0;font-size:19.6px;text-transform:uppercase;margin:0}.main-menu li{margin:0 14px}.main-menu li .navlink{border-bottom:2px solid #fff}.main-menu li a{border-bottom:2px solid #fff}.main-menu li a:hover{border-bottom:2px solid #78a1bd}.main-menu .cart{display:none}@media (max-width: 767px){.main-menu{opacity:0;height:0}.mobile-menu{opacity:1;height:auto;transition:opacity 1000ms ease;display:flex;flex-direction:column;align-items:flex-end;margin-top:1rem}.mobile-menu .cart{display:flex !important}.mobile-menu .navlink.active{border-bottom:2px solid #78a1bd}}

.cart-qty{position:relative}.cart-items{position:absolute;right:0;transform:translate(100%, -15%);font-size:18px;font-weight:bold}

.navbar{display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;margin-bottom:20px;transition:.3s}.navbar .cart-icon{color:#333;margin-left:14px;font-size:28px}.navbar .cart-icon:hover{color:#78a1bd}.navbar .cart-icon.active{color:#78a1bd}.navbar-wrapper{display:flex;justify-content:center;align-items:center}@media (max-width: 767px){.nav-mobile{flex-direction:column-reverse;align-items:flex-end;width:100%}.cart-icon{display:none}.burger{color:#333;font-size:25.2px}.burger.active{color:#78a1bd}}

.container-fluid{width:100%;max-width:1000px;margin:0 auto;padding:14px 28px}

.footer{display:flex;justify-content:space-between;padding:1.3rem 1rem;font-size:15.4px;margin:0 auto}.footer .copy{margin:0}.footer-menu{list-style:none;display:flex;justify-content:space-around;margin:0;text-transform:uppercase}.footer-menu li{padding-left:1.4rem}@media (max-width: 767px){.footer{flex-direction:column-reverse;margin-top:0;font-size:14px}.footer-menu{padding:0;justify-content:space-around}.footer-menu li{padding-left:0.2rem}.footer .copy{display:flex;justify-content:center;margin-top:0.4rem}.footer .copy a{padding-left:0.5rem}}@media (max-width: 479px){.footer-menu{display:none}}

.products-list{display:flex;flex-wrap:wrap;justify-content:center}

.product-summary{margin:1rem;transition:ease .2s;cursor:pointer;padding:0}.product-summary:hover{border-radius:5px;box-shadow:0 0 1.1rem rgba(0,0,0,0.2);transform:scale(1.08)}.badge{font-weight:normal;width:4rem;font-size:14px;margin:0.6rem 0.6rem 0 0.6rem}.card-title{font-weight:bold;font-size:15.4px}.card-subtitle{font-size:15.4px}.non-badge{height:1.9rem}@media (max-width: 991px){.card-title{font-size:10.76923px}.card-subtitle{font-size:11.66667px}}@media (max-width: 767px){.card-title{font-size:15.4px}.card-subtitle{font-size:15.4px}}

.lds-ellipsis{display:block;position:relative;width:64px;height:64px;margin:0 auto}.lds-ellipsis div{position:absolute;top:27px;width:11px;height:11px;border-radius:50%;background:#333;-webkit-animation-timing-function:cubic-bezier(0, 1, 1, 0);animation-timing-function:cubic-bezier(0, 1, 1, 0)}.lds-ellipsis div:nth-child(1){left:6px;-webkit-animation:lds-ellipsis1 0.6s infinite;animation:lds-ellipsis1 0.6s infinite}.lds-ellipsis div:nth-child(2){left:6px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(3){left:26px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(4){left:45px;-webkit-animation:lds-ellipsis3 0.6s infinite;animation:lds-ellipsis3 0.6s infinite}@-webkit-keyframes lds-ellipsis1{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes lds-ellipsis1{0%{transform:scale(0)}100%{transform:scale(1)}}@-webkit-keyframes lds-ellipsis3{0%{transform:scale(1)}100%{transform:scale(0)}}@keyframes lds-ellipsis3{0%{transform:scale(1)}100%{transform:scale(0)}}@-webkit-keyframes lds-ellipsis2{0%{transform:translate(0, 0)}100%{transform:translate(19px, 0)}}@keyframes lds-ellipsis2{0%{transform:translate(0, 0)}100%{transform:translate(19px, 0)}}

.pagination{display:flex;justify-content:flex-end;margin:15px 20px 0 0}.pagination__list{display:flex;justify-content:center;list-style-type:none;font-size:19.6px;padding:0}.pagination__list__item{width:28px;height:28px;margin:7px;padding:15px;justify-content:center;align-items:center;cursor:pointer;border-radius:5px;transition:.2s;color:#666}.pagination__list__item:hover{color:#333}.pagination__list__item--active{color:#3498db}

.home-page{margin-top:40px}.counter{margin:1.1rem 0 1rem 0;padding-top:0.4rem;border-top:1px solid #666}

.carousel{margin-bottom:60px}.carousel-inner{border-radius:.25rem}

.discount{margin-bottom:2.8rem;padding:1.3rem 0;text-align:center;height:5.5rem;border-radius:.25rem}.discount button{width:50%;height:100%;font-size:21px}.discount-text{display:flex;justify-content:center;align-items:center}.discount-code{font-weight:bold;font-size:1.5rem;width:10rem;text-align:center;margin-left:1rem}@media (max-width: 899px){.discount button{width:50%}}@media (max-width: 767px){.discount{height:3rem;border:none;margin:0;padding:0}.discount button{width:80%;height:auto;font-size:14px}.discount-text,.discount-code{font-size:14px;text-align:center}.model-title{font-size:14px}}

.sort-by{display:flex;flex-direction:column;align-items:flex-start;margin-top:1rem}.sort-by .btn{border:none;border-radius:0;border-bottom:1.5px solid #fff;background-color:inherit;color:#666;outline:none;box-shadow:none;padding:0;margin:0 0 0.6rem 0.6rem}.sort-by .btn:hover{border-bottom:1.5px solid #78a1bd}.sort-by .btn.active.focus,.sort-by .btn.active:focus,.sort-by .btn.focus,.sort-by .btn:active.focus,.sort-by .btn:active:focus,.sort-by .btn:focus{border:none;outline:none;box-shadow:none;background-color:inherit;color:#333;font-weight:bold;border-bottom:1.5px solid #fff}

.page-title{margin:14px 0;font-size:32.2px;font-weight:600;position:relative}

.faq-wrapper{padding:1rem}@media (max-width: 479px){.faq{text-align:center}.faq .faq-wrapper{font-size:14px}}

.storepolicy-wrapper{padding:1rem;text-align:justify}@media (max-width: 479px){.storepolicy{text-align:center}.storepolicy .storepolicy-wrapper{font-size:14px}}

.contact-wrapper{padding:1rem}.contact-wrapper .social-media a{padding:1rem;font-size:30.8px;color:#666;transition:ease .2s}.contact-wrapper .social-media a:hover{color:#78a1bd}@media (max-width: 479px){.contact{text-align:center}.contact .contact-wrapper{font-size:14px}.contact .contact-wrapper .social-media a{padding:0.5rem}}

.qty-items{display:flex;flex-direction:column;align-items:center}.qty-items button{border:none;background:inherit}.qty-items .qty-buttons{display:flex;flex-direction:row;justify-content:center;align-items:center;margin-top:0.9rem}.qty-items .qty-buttons button{margin:0.5rem 1rem;width:35px;height:35px}.qty-items .qty-buttons .qty-minus{font-size:22.4px}.qty-items .qty-buttons .qty-product{display:flex;justify-content:center;align-items:center;min-width:35px;min-height:35px;background:#ddd}.qty-items .btn-delete{font-size:12.72727px;width:4rem}

.cart-wrapper{padding:1rem;font-size:15.4px}.cart-wrapper .cart-product{align-items:center;margin-bottom:0.7rem}.cart-wrapper .cart-product img{width:100%}.cart-wrapper .cart-product .product-price{display:flex;justify-content:center}.cart-summary{padding:1rem;display:flex;flex-direction:column;align-items:flex-end}.cart-summary input-group{margin-bottom:0.9rem;max-width:-webkit-max-content;max-width:-moz-max-content;max-width:max-content;height:40px}.cart-summary input-group input{height:40px}.cart-summary .alert{font-size:15.4px;padding:0 1rem;min-height:40px;display:flex;align-items:center}.cart-summary .alert-warning{cursor:pointer}@media (max-width: 767px){.cart{text-align:center}.cart .cart-product img{width:50%}}@media (max-width: 479px){.cart{text-align:center}.cart .cart-short-text{font-size:14px}}

.back-btn{margin:0 0 1rem 1rem}.back-icon{margin-right:0.4rem}

.summary-wrapper{padding:1rem}.summary-wrapper .scroll-wrapper{display:block;width:100%;overflow-x:auto;white-space:nowrap}.summary-wrapper .scroll-wrapper .table{color:#666}.summary-wrapper .back-btn{margin-left:0;display:flex;align-items:center}.summary-wrapper .summary-footer{display:flex;flex-direction:column;align-items:flex-end}.summary-wrapper .summary-footer .summary-buttons{margin-top:1rem;width:100%;display:flex;justify-content:space-between}@media (max-width: 479px){.summary{text-align:center}.summary .summary-wrapper{font-size:14px}}

